.input {
    max-height: 62px;
    line-height: 62px;
    width: calc(100% - 100px);
    outline: none;
    padding: 0 18px;
    color: #1C1C26;
    border: 2px solid white;
    margin-right: 30px;
    border-radius: 12px;
    font-size: 24px;
}

@media (max-width: 425px) {

    .input {
        font-size: 10px;
        max-height: 25px;

    }
}